import { Api } from '../base/api';
export class AppApi extends Api {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    getAllItem() {
        return this.get('/product/').then((data) => data.items.map((item) => (Object.assign(Object.assign({}, item), { image: this.cdn + item.image.replace('.svg', '.png') }))));
    }
    getItemId(itemId) {
        return this.get(`/product/${itemId}`).then((item) => (Object.assign(Object.assign({}, item), { image: this.cdn + item.image.replace('.svg', '.png') })));
    }
    postItem(order) {
        return this.post('/order', order).then((data) => data);
    }
}
