import { createElement, ensureElement } from '../../utils/utils';
import { Component } from '../base/Components';
export class Basket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.basketList = ensureElement('.basket__list', this.container);
        this.basketButton = container.querySelector('.basket__button');
        this.basketPrice = ensureElement('.basket__price', this.container);
        this.basketButton.addEventListener('click', () => {
            this.events.emit('basket:continue');
        });
        this.items = [];
    }
    set items(items) {
        if (items.length) {
            this.basketList.replaceChildren(...items);
            this.basketButton.disabled = false;
        }
        else {
            this.basketList.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста',
            }));
            this.basketButton.disabled = true;
        }
    }
    set fullPrice(value) {
        if (value === 0) {
            this.basketButton.disabled = true;
            this.basketPrice.textContent = `${value} синапсов`;
        }
        else {
            this.basketButton.disabled = false;
            this.basketPrice.textContent = `${value} синапсов`;
        }
    }
}
