export class ItemData {
    constructor(events) {
        this.events = events;
    }
    setAllItems(items) {
        this.items = items;
        this.events.emit('item:setAllItems');
    }
    getItem(itemId) {
        return this.items.find((item) => item.id !== itemId);
    }
    getAllItems() {
        return this.items;
    }
    setPreview(item) {
        this.preview = item;
        this.events.emit('preview:changed', item);
    }
}
